import React from 'react';
import s from './Card.module.css';
import {NavLink} from 'react-router-dom';

type CardProps = {
    poster: string | null
    title: string
    type: string
    year: string
    imdbID: string
    callback?: () => void
}

const Card : React.FC<CardProps> = ({poster, title, type, year, imdbID, callback, ...restProps}) => {
    return (
        <div className={s.card}>
            <div className={s.poster}>
                <img alt='poster' src={poster ? poster : ''}/>
            </div>
            <div className={s.description}>
                <div className={s.details}>
                    <div className={s.imdb}>ImdbID: {imdbID}</div>
                    <div className={s.title}>"{title}"</div>
                    <div className={s.year}>{year}</div>
                </div>
                <div className={s.button}>
                    <NavLink to="/card/card-description">More details</NavLink>
                </div>
            </div>
        </div>
    )
}

export const CardItem = React.memo(Card)